import React from "react";
import styles  from "../email.module.css";
class EmailForgotPassword extends React.Component {
  render() {
    return (
    
      <div>
        <div className={styles.first}>
        <img src='https://drive.google.com/uc?id=1RO5uiDM6ESaRI2nOdptNJy_v1nMWtXW3' alt='Google' className={styles.logo} /> 
        <br/>
        <h1>Ha solicitado reestablecer la contrasea</h1>
        <br/>
        <p>Correo</p>
        <br/>
        <p>Este proceso tiene valides por 24 horas. Si no realizo esta solicitud por favor haga 
caso omiso a este correo, de lo contrario haga clic en Reestablecer Contrasea</p>
<br/>
        <button type="button" className={styles.buttonCustom}> Reestablecer contrasea</button>
        </div>
        
      </div>
    
  );
  }
 
};

export default EmailForgotPassword;
